import { lodashCamelCase, isEmpty } from 'lodash';

class Utility {
  /**
    * Get element from array by property
    *
    * @param array
    * @param mixed
    * @param string
    * @return mixed
    */
  static getElementFromArrayByProperty(elements, value, property = 'id') {
    let selectedScriptIndex = false;

    const selectedElement = elements.filter((element, index) => {
      let filtered = false;

      if (element[property] === value) {
        filtered = true;
        selectedScriptIndex = index;
      }

      return filtered;
    });

    return { element: selectedElement.shift(), index: selectedScriptIndex };
  }

  /**
    * delete element from array by property value
    *
    * @param array
    * @param mixed
    * @param string
    * @return mixed
    */
  static removeElementFromArrayByProperty(elements, value, property = 'id') {
    return elements.filter((element, index) => element[property] && element[property] !== value);
  }

  /**
    * Safly push element to array
    *
    * @param elements
    * @param element
    * @param string
    * @return array
    */
  static safelyPushElementToArray(elements, element, property = 'id') {
    const elementInfo = Utility.getElementFromArrayByProperty(elements, element[property]);

    if (!elementInfo.index && Array.isArray(elements)) {
      elements.push(element);
    }

    return elements;
  }

  /**
     * @description
     * Determines if a reference is an `Object`. Unlike `typeof` in JavaScript, `null`s are not
     * considered to be objects. Note that JavaScript arrays are objects.
     *
     * @param {*} value Reference to check.
     * @returns {boolean} True if `value` is an `Object` but not `null`.
     */
  static isObject(value) {
    return value !== null && typeof value === 'object';
  }

  /**
     *
     * @description
     * Determines if a reference is a `String`.
     *
     * @param {*} value Reference to check.
     * @returns {boolean} True if `value` is a `String`.
     */
  static isString(value) {
    return typeof value === 'string';
  }

  /**
     *
     * @description
     * Format the date as {2017-04-03 / 14:58:00}
     *
     * @param date.
     * @returns String
     */
  static getFormattedDate(date) {
    date = new Date(date);
    const prefixAdder = (data) => (data < 10 ? `0${data}` : data);

    return `${date.getFullYear()}-${prefixAdder((date.getMonth() + 1))}-${prefixAdder(date.getDate())}
                / ${prefixAdder(date.getHours())}:${prefixAdder(date.getMinutes())}:${prefixAdder(date.getSeconds())}`;
  }

  /**
     *
     * @description
     * Compare two objects
     *
     * @param object1
     * @param object2.
     * @returns boolean
     */
  static equals(o1, o2) {
    if (o1 === o2) return true;
    if (o1 === null || o2 === null) return false;
    // eslint-disable-next-line no-self-compare
    if (o1 !== o1 && o2 !== o2) return true; // NaN === NaN
    const t1 = typeof o1; const t2 = typeof o2; let key; let
      keySet;
    if (t1 === t2 && t1 === 'object') {
      keySet = Object.create(null);
      for (key in o1) {
        if (key.charAt(0) === '$' || Utility.isFunction(o1[key])) continue;
        if (!Utility.equals(o1[key], o2[key])) return false;
        keySet[key] = true;
      }
      for (key in o2) {
        if (!(key in keySet)
                && key.charAt(0) !== '$'
                && Utility.isDefined(o2[key])
                && !Utility.isFunction(o2[key])) return false;
      }
      return true;
    }
    return false;
  }

  /**
     * Determines if a value is a regular expression object.
     *
     * @private
     * @param {*} value Reference to check.
     * @returns {boolean} True if `value` is a `RegExp`.
     */
  static isRegExp(value) {
    return toString.call(value) === '[object RegExp]';
  }

  /**
     * @description
     * Determines if a value is a date.
     *
     * @param {*} value Reference to check.
     * @returns {boolean} True if `value` is a `Date`.
     */
  static isDate(value) {
    return toString.call(value) === '[object Date]';
  }

  /**
     * @description
     * Determines if a reference is a `Function`.
     *
     * @param {*} value Reference to check.
     * @returns {boolean} True if `value` is a `Function`.
     */
  static isFunction(value) {
    return typeof value === 'function';
  }

  /**
     * @description
     * Determines if a reference is defined.
     *
     * @param {*} value Reference to check.
     * @returns {boolean} True if `value` is defined.
     */
  static isDefined(value) {
    return typeof value !== 'undefined';
  }

  /**
     * @description
     * Check current browser is ie
     *
     * @returns {boolean} True if `value` is defined.
     */
  static isIE() {
    return /* @cc_on!@ */false || !!document.documentMode;
  }

  /**
     * @description
     * Check current browser is Edge
     *
     * @returns {boolean} True if `value` is defined.
     */
  static isEdge() {
    return !Utility.isIE() && !!window.StyleMedia;
  }

  /**
     * @description
     * Get image as base 64
     *
     * @param image
     * @param ext
     * @returns string
     */
  static getImageAsBase64(image, ext) {
    return `data:image/${ext};base64,${image}`;
  }

  /**
     * @description
     * Get string as camel case
     *
     * @param string
     * @returns string
     */
  static camelCase(str) {
    const convertedStr = lodashCamelCase(str);
    return convertedStr.charAt(0).toUpperCase() + convertedStr.slice(1);
  }

  /**
     * @description
     * Get string as camel case
     *
     * @param string
     * @returns string
     */
  static checkValidation(props, value) {
    const validation = (props.form && props.form.validation(props.fieldName));
    const message = validation ? props.state.inputErrors[props.fieldName].message : '';
    const classValidation = `form-group ${validation ? 'feild-wrong' : ''} ${(Array.isArray(value) ? value.length : value) && !validation ? 'feild-sucess' : ''}`;
    return classValidation;
  }

  /**
     * @description
     * Get string as camel case
     *
     * @param string
     * @returns string
     */
  static setProps(props) {
    this.newProps = props;
  }

  /**
     * @description
     * Get string as camel case
     *
     * @param string
     * @returns string
     */
  static checkUndefined(fields) {
    let change = [];
    for (const field of fields) {
      if (field.length > 1) {
        for (const extra of field) {
          if (field.length > 0) {
            if (change.length === 0) {
              change = this.newProps;
            }
            if (change[extra] === undefined) {
              return false;
            }
            change = change[extra];
          } else if (this.newProps[extra] === undefined) {
            return false;
          }
        }
        return true;
      }
      if (this.newProps[field] === undefined) {
        return false;
      }
      return true;
    }
  }

  /**
     * If given value is empty or undefined return the default value
     * @param {*} value
     * @param {*} defaultVal
     */
  static emptyToDefault(value, defaultVal) {
    return !isEmpty(value) ? value : defaultVal;
  } 
}

export default Utility;

export function generateUnqId(lable){
  return lable||'' + (new Date()).getTime();
}